.items-menu{
    position: fixed;
    top: 0px;
    left: -236px;
    color: var(--color-text-primary);
    background-color: var(--color-font-primary);
    width: 235px;
    z-index: 20;
}
.items-menu .logo{
    display: flex;
    align-items: center;
    padding: 10px 5px;
}
.items-menu .items::-webkit-scrollbar{
    width: 9px;
}
.items-menu .items:hover::-webkit-scrollbar-thumb{
    background: var(--color-text-cuartiary);
    border-radius: 30px;
}
.items-menu .items{
    height: 90vh;
    padding: 10px 20px 0px 10px;
    overflow-x: hidden;
}
.items-menu .item{
    display: flex;
    align-items: center;
    border-radius: 11px;
    width: 200px;
    padding: 9px 2px 9px 7px;
    margin-bottom: 3px;
    text-decoration: none;
    color: var(--color-text-primary);
}
.items-menu .item:hover{
    cursor: pointer;
    background-color: var(--color-font-cuartiary);
}
.items-menu .item:nth-child(1){
    background-color: var(--color-font-cuartiary);
}
.items-menu .item span{
    font-size: 14px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
    color: var(--color-text-primary);
    margin-left: 16px;
}
.items-menu .items .linea-item{
    margin: -6px 0 10px 0;
    color: var(--color-font-hover-buttons);
}
.items-menu .item ion-icon{
    width: 23px;
    height: 23px;
    margin-left: 8px;
}
.items-menu .sub-title-item{
    padding: 5px 2px 5px 7px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
    font-size: 16px;
    text-align: start;
}
.items-menu .item-subscription img{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    margin-left: 6px;
}
.items-menu .sub-title-footer-item{
    color: var(--color-text-secondary);
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
    font-size: 13.5px;
    text-align: start;
    padding-left: 10px;
    margin-bottom: 10px;
    margin-right: 11px;
}
.items-menu .item-footer{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 12px;
    padding-bottom: 10px;
    padding-left: 5px;
    color: var(--color-text-cuartiary);
}
.items-menu .item-footer ion-icon{
    width: 12px;
    height: 12px;
    margin-top: -4px;
}
@keyframes animate_nav_items_menu_view{
    0%{
        left: -235px;
    }
    100%{
        left: 0px;
    }
}
@keyframes animate_nav_items_menu_hidden{
    0%{
        left: 0px;
    }
    100%{
        left: -235px;
    }
}



/**----------Iniciar sesion----------**/
.acceder-toggle{
    padding-bottom: 5px;
    margin-left: 17px;
}
.acceder-toggle p{
    font-size: 13px;
    font-weight: 500;
    font-family: Arial, Helvetica, sans-serif;
    padding-right: 5px;
    line-height: 20px;
    letter-spacing: 0.3px;
    margin-bottom: 12px;
}
.acceder-toggle a{
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: Arial, Helvetica, sans-serif;
    outline: none;
    border: 0.1px solid var(--color-font-hover-buttons);
    padding: 17px 16px 17px 10px;
    background-color: var(--color-font-primary);
    color: var(--color-border-input-search);
    border-radius: 30px;
    width: 110px;
    height: 35px;
    text-decoration: none;
}
.acceder-toggle a:hover{
    cursor: pointer;
    background-color: var(--color-font-hover-button-reintentar);
}
.acceder-toggle a ion-icon{
    width: 23px;
    height: 23px;
}
.acceder-toggle a span{
    padding-top: 1.5px;
    font-size: 14.5px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    margin-left: 5px;
}



